import HotSale from "../Home/HotSale";
import HeaderBack from '../Common/HeaderBack'
export default {
  name: 'actDetail',
  components: {
    myHeader: HeaderBack,
    myHotSale: HotSale ,
  },
  data () {
    return {
      header: {
        HBL_icon:"icon_back", 
        HBC_text:"", 
        HBR_icon: "icon_share",
        HBR_type: 'share',
        HBR_show: true,
      },
      NAME: 'Look Activity',
      IMG:  require('../../assets/images/home/activity_02.jpg'),
      BIMG: require('../../assets/images/home/activity_01.jpg'),
      DESCRIPTION: '好便宜啊,大家都来看看!!!',
      // 热卖商品数据
      productList:[],

      showHideOnBlur: false,
    }
  },
  methods: {
    goToAll () {
      this.$router.push({path: '/mainpage/all'});
    },
    showShareFn () {
      this.showHideOnBlur = true;
    },
  },
  created () {
    let id = this.$route.params.activityId;
    let allGoods = this.$store.state.allGoods;
    // 数据加载 
    comTools.showLoading(this);
    setTimeout(() => {
      allGoods.forEach((ele,i) => {
        if (i > 7)
          return
        this.productList.push(ele);
      });
      // 改变主题
      this.$refs.myHeader.changeText('HBC_text',this.NAME)
      // 数据加载 结束
      comTools.hideLoading(this);
    }, 500)
  }
}



